import * as types from './mutation-types.js'

let actions = {
  toastrSuccess ({commit}, payload) {
    commit(types.TOASTR_SUCCESS, payload)
  },
  toastrError ({commit}, payload) {
    commit(types.TOASTR_ERROR, payload)
  }
}

export default actions



