import * as types from './mutation-types.js'

let mutations = {
  [types.TOASTR_SUCCESS] (state, payload) {
    state.toastr = { type: 'success', title: 'StrEAMer', content: payload }
    state.isDisplay = true
  },
  [types.TOASTR_ERROR] (state, payload) {
    state.toastr = { type: 'error', title: 'StrEAMer', content: payload }
    state.isDisplay = true
  }
}

export default mutations



